.flex-container-column {
    height: 100vh;

    display: flex;
    flex-direction: column;

    header {
        justify-self: flex-start;
        background: red;
        height: 20%;
        width: 100%;
    }

    main {
        background: green;
        width: 100%;
        flex-grow: 1;
        overflow: auto;
    }

    footer {
        background: yellow;
        height: 20%;
        width: 100%;
        justify-self: flex-end;
    }
}